.team-main-container {
  display: flex;
  flex-direction: column;
  background-color: white;
  flex: 1;
  max-width: 560px;
  box-shadow: 0px 2px 4px 1px rgba(0, 0, 0, 0.28);
  border-radius: 7px;
  padding: 40px;
  margin-left: auto;
  margin-right: auto;
}

.team-main-container header {
  margin: 30px 0px;
}

.team-main-container header h1 {
  font-size: 38px;
  font-weight: normal;
  margin: 15px 0px;
}


.team-dogtag{
  position : relative;
  background-color: #F5F5F5;
  border-radius: 4px;
  display: grid;
  grid-template-columns: min-content 1fr;
  grid-template-rows: max-content 1fr;
  max-width: 200px;
  color : black;
  text-decoration: none;
}

.team-dogtag img{
  position: absolute;
  top: 10px;
  right : 10px;
}