.side-navbar {
  box-shadow: 0px 2px 4px 1px rgba(0, 0, 0, 0.28);
  transition: background 0.25s;
  display: flex;
  position: absolute;
  z-index: 1;
  right: 0px;
  top: 70px;
  bottom:0px;
  left: 0px;
}

.placeholder{
  position: relative;
}

.side-navbar .nav-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  width: 100%;
  position: relative;
  font-size: 9px;
  font-weight: bold;
  text-decoration: none;
  padding: 10px 0px;
}

.side-navbar .nav-link p {
  margin: 0px;
}

.active {
  opacity: 1;
}

.inactive {
  opacity: 0.5;
}

.nav-link :hover {
  opacity: 1;
}


